import { ACCOUNT_LIST_SUCCESS } from '../../constants/admin/actionTypes'

const initialState = {
  accountList: [],
}

const AccountReducer = (state = initialState, action) => {
  switch (action.type) {
    case ACCOUNT_LIST_SUCCESS:
      return {
        ...state,
        accountList: action.data,
      }
    default:
      return state
  }
}

export default AccountReducer
