import Container from 'react-bootstrap/Container'
import { Link, useNavigate } from 'react-router-dom'
import { loginAction, logoutAction } from '../action/auth.action'
import { useDispatch, useSelector } from 'react-redux'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUserCircle } from '@fortawesome/free-regular-svg-icons'
import Web3 from 'web3'

function Header(props) {
  const auth = useSelector(({ auth }) => auth)
  const dispatch = useDispatch()
  const navigate = useNavigate()

  // useEffect(() => {
  //   window.ethereum.on('accountsChanged', (account) => {
  //     console.log('changed')
  //     console.log(account)
  //   })
  // }, [])

  const onLogout = () => {
    dispatch(logoutAction())
  }

  const connectWallet = async () => {
    if (!window.ethereum) {
      alert('Please set up metamask')
      window.location.href =
        'https://chrome.google.com/webstore/detail/metamask/nkbihfbeogaeaoehlefnkodbefgpgknn?hl=en'
    }

    window.web3 = new Web3(Web3.givenProvider)
    await window.ethereum.enable()

    const accounts = await window.web3.eth.getAccounts()
    if (accounts.length) {
      dispatch(loginAction({ connectedAccount: accounts[0] }, navigate))
    }
  }
  return (
    <>
      <div className="header-padding">
        <div bg="dark" variant="light">
          <Container>
            <div className="d-flex justify-content-left">
              <div>
                <Link to="/">
                  <img
                    alt=""
                    src="/logo.png"
                    width="100"
                    height="100"
                    className="d-inline-block align-top"
                  />
                </Link>{' '}
              </div>
              <div className="row flex-column justify-content-center text-white logo-assist-padding h2 flex-grow-1">
                <strong>DEGEN BOT</strong>
                <p className="font-size-1">HIGE PERFORMANCE MINTING BOT</p>
              </div>
              {auth.isAuthenticated ? (
                <div className="d-flex text-white h6 text-end logo-assist-padding align-items-center">
                  <div className="font-size-3">
                    <FontAwesomeIcon icon={faUserCircle} />
                  </div>
                  <div>
                    <Link
                      to="/"
                      className="text-decoration-none text-primary px-1"
                    >
                      USER
                    </Link>
                  </div>
                  <div style={{ borderLeft: '1px solid white' }}>
                    <Link
                      to="/"
                      className="text-white text-decoration-none px-1"
                      onClick={onLogout}
                    >
                      LOGOUT
                    </Link>
                  </div>
                </div>
              ) : (
                <div className="text-white h6 text-end logo-assist-padding align-middle d-flex align-items-center">
                  <div
                    className="button text-white text-decoration-none"
                    onClick={connectWallet}
                  >
                    LOGIN
                  </div>
                </div>
              )}
            </div>
          </Container>
        </div>
      </div>
      <div>{props.children}</div>
    </>
  )
}

export default Header
