import { Request } from '../basic'
import { put, takeLatest } from 'redux-saga/effects'
import {
  ACCOUNT_LIST_REQUEST,
  ACCOUNT_LIST_SUCCESS,
  ACCOUNT_SAVE_REQUEST,
  ERROR_OCCURED,
  SUCCESS_OCCURED,
  CHANGE_CURRENT_COUNT,
  ACCOUNT_REMOVE_REQUEST,
} from '../../constants/admin/actionTypes'
import { errorOccured } from '../../action/error.action'

function* AccountSave(action) {
  try {
    const responseData = yield Request('admin/account/add', 'POST', action.data)
    if (responseData.success) {
      yield put({
        type: SUCCESS_OCCURED,
        message: 'Add account successfully!',
      })

      yield put({
        type: ACCOUNT_LIST_SUCCESS,
        data: responseData.success.accounts,
      })

      yield put({
        type: CHANGE_CURRENT_COUNT,
        payload: responseData.success.currentCount,
      })
    } else yield put(errorOccured(responseData))
  } catch (err) {
    yield put({
      type: ERROR_OCCURED,
      message: 'Already Exist',
    })
  }
}

function* AccountRemove(action) {
  try {
    const responseData = yield Request(
      'admin/account/remove',
      'DELETE',
      action.data,
    )
    if (responseData.success) {
      yield put({
        type: SUCCESS_OCCURED,
        message: 'Remove account successfully!',
      })

      yield put({
        type: ACCOUNT_LIST_SUCCESS,
        data: responseData.success.accounts,
      })
    } else yield put(errorOccured(responseData))
  } catch (err) {
    yield put({
      type: ERROR_OCCURED,
      message: 'While sending request to server, something went wrong.',
    })
  }
}

function* GetAccounts(action) {
  try {
    const responseData = yield Request('admin/account', 'GET')
    if (responseData) {
      console.log(responseData)
      yield put({ type: ACCOUNT_LIST_SUCCESS, data: responseData })
    }
  } catch (err) {
    console.log(err)
  }
}

export default function* accountSaga() {
  yield takeLatest(ACCOUNT_SAVE_REQUEST, AccountSave)
  yield takeLatest(ACCOUNT_REMOVE_REQUEST, AccountRemove)
  yield takeLatest(ACCOUNT_LIST_REQUEST, GetAccounts)
}
