import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Navigate, useNavigate } from 'react-router-dom'
import {
  accountList,
  accountRemove,
  accountSave,
} from '../../action/admin/account.action'
import { userList, userUpdate } from '../../action/admin/user.action'
import { logoutAction } from '../../action/admin/auth.action'
import { errorOccured } from '../../action/error.action'

const Dashboard = () => {
  const [newAccount, setNewAccount] = useState('0x')
  const auth = useSelector(({ adminAuth }) => adminAuth)
  const accounts = useSelector(({ adminAccount }) => adminAccount.accountList)
  const users = useSelector(({ adminUser }) => adminUser.userList)
  const dispatch = useDispatch()
  const navigate = useNavigate()

  useEffect(() => {
    if (auth.isAuthenticated) {
      dispatch(accountList())
      if (auth.user.role === 'super_admin') {
        dispatch(userList())
      }
    }
  }, [auth.isAuthenticated, auth.user, dispatch])

  const addNewAccount = () => {
    if (
      auth.user.maxCount <= auth.user.currentCount &&
      auth.user.role !== 'super_admin'
    ) {
      dispatch(
        errorOccured(
          'You are limited the available account count by ' +
            auth.user.maxCount,
        ),
      )
    } else {
      dispatch(
        accountSave({ connectedAccount: newAccount, adminId: auth.user._id }),
      )
      setNewAccount('0x')
    }
  }

  const removeAccount = (connectedAccount) => () => {
    dispatch(accountRemove({ connectedAccount }))
  }

  const onInputNewAccount = (e) => {
    setNewAccount(e.target.value)
  }

  const changeApproveStatus = (user) => () => {
    dispatch(userUpdate({ user: { ...user, approved: !user.approved } }))
  }

  const logOut = () => {
    dispatch(logoutAction())
    navigate('/admin/login')
  }

  const onMaxCountChange = (user) => (e) => {
    dispatch(
      userUpdate({ user: { ...user, maxCount: Number(e.target.value) } }),
    )
  }

  if (auth.isAuthenticated) {
    return (
      <div className="p-4 text-white">
        <div>
          <div className="d-flex">
            <p className="title-1 flex-grow-1">ACCOUNT LIST</p>
            <div className="button text-warning" onClick={logOut}>
              LOGOUT
            </div>
          </div>
          <div className="list-box height-3 p-4">
            <div className="d-flex justify-content-center">
              <div className="nft-address-box">
                <input
                  type="text"
                  value={newAccount}
                  onChange={onInputNewAccount}
                ></input>
                <button className="nft-address-search" onClick={addNewAccount}>
                  ADD
                </button>
              </div>
            </div>
            {accounts.map((account, index) => (
              <div key={index} className="d-flex w-70 m-auto mt-2">
                <div className="flex-grow-1">{account.connectedAccount}</div>
                <div
                  className="button text-danger"
                  onClick={removeAccount(account.connectedAccount)}
                >
                  REMOVE
                </div>
              </div>
            ))}
          </div>
        </div>
        {auth.user.role === 'super_admin' ? (
          <div className="mt-4">
            <p className="title-1">ADMINS</p>
            <div className="list-box height-3 p-4">
              {users.map((user, index) => (
                <div
                  key={index}
                  className="d-flex w-70 m-auto mt-2 align-items-center"
                >
                  <div className="flex-grow-1">{user.username}</div>
                  <div className="flex-grow-1">
                    {user.approved ? 'Approved' : 'Not Approved'}
                  </div>
                  <div
                    className="button text-danger"
                    onClick={changeApproveStatus(user)}
                  >
                    {user.approved ? 'Not Approve' : 'Approve'}
                  </div>
                  <div className="p-2">
                    <label htmlFor="maxCount">MaxCount</label>
                    <input
                      id="maxCount"
                      type={'number'}
                      min={0}
                      value={user.maxCount}
                      onChange={onMaxCountChange(user)}
                    />
                  </div>
                </div>
              ))}
            </div>
          </div>
        ) : (
          <></>
        )}
      </div>
    )
  } else {
    return <Navigate to="/admin/login" />
  }
}

export default Dashboard
