import { USER_LIST_SUCCESS } from '../../constants/admin/actionTypes'

const initialState = {
  userList: [],
}

const AccountReducer = (state = initialState, action) => {
  switch (action.type) {
    case USER_LIST_SUCCESS:
      return {
        ...state,
        userList: action.data,
      }
    default:
      return state
  }
}

export default AccountReducer
