import { all } from 'redux-saga/effects'
import authSaga from './auth.saga'
import accountSaga from './account.saga'
import transactionSaga from './transaction.saga'
import adminAuthSaga from './admin/auth.saga'
import adminAccountSaga from './admin/account.saga'
import adminUserSaga from './admin/user.saga'

export default function* rootSaga() {
  yield all([
    adminAuthSaga(),
    adminAccountSaga(),
    adminUserSaga(),
    authSaga(),
    accountSaga(),
    transactionSaga(),
  ])
}
