import { useCallback, useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Navigate } from 'react-router-dom'
import clsx from 'clsx'
import Web3 from 'web3'
import { ethers } from 'ethers'
import Header from '../components/Header'
import {
  accountSave,
  accountRemove,
  contractABIFromAddress,
  mintDataSave,
  accountList,
} from '../action/account.action'
import { Modal, Button } from 'react-bootstrap'
import { errorOccured, successOccured } from '../action/error.action'
import env from '../env.json'
import {
  transactionList,
  transactionRemove,
} from '../action/transaction.action'
const web3 = new Web3(new Web3.providers.HttpProvider(env.API_KEY))

function Home() {
  const auth = useSelector(({ auth }) => auth)
  const user = useSelector((state) => state.auth.user)
  const accounts = useSelector((state) => state.account.accountList)
  const transactions = useSelector((state) => state.transaction.transactionList)
  const contractABI = useSelector((state) => state.account.contractABI)
  const contractFunction = useSelector(
    (state) => state.account.contractFunction,
  )
  // const boolVariables = useSelector((state) => state.account.boolVariables)
  const monitorFunctions = useSelector(
    (state) => state.account.monitorFunctions,
  )

  const [taskName, setTaskName] = useState('')
  const [selectedTab, setSelectedTab] = useState(1)
  const [show, setShow] = useState(false)
  const [value, SetValue] = useState(0)
  const [gasPriceAuto, SetGasPriceAuto] = useState(true)
  const [gasPrice, SetGasPrice] = useState(30)
  const [gasLimitAuto, SetGasLimitAuto] = useState(true)
  const [gasLimit, SetGasLimit] = useState(30000)
  // const [tokenPrice, SetTokenPrice] = useState(0)
  // const [tokenAmount, SetTokenAmount] = useState(0)
  const [monitorMethod, SetMonitorMethod] = useState('time')
  const [monitorFunction, SetMonitorFunction] = useState('')
  const [sendDate, SetSendDate] = useState('2022-7-15')
  const [sendTime, SetSendTime] = useState('00:00:00')
  const [privateKey, SetPrivateKey] = useState('0x')
  // const [boolValue, SetBoolValue] = useState('None')
  const [contractAddress, SetContractAddress] = useState('0x')
  const [mintFunction, SetMintFunction] = useState('')
  const [mintParameters, SetMintParameters] = useState([])
  const [importKey, SetImportKey] = useState('0x')
  const [amounts, setAmounts] = useState(Array(accounts.length).fill(0))
  const dispatch = useDispatch()

  useEffect(() => {
    let now = new Date()
    let year = now.getFullYear()
    let month =
      now.getMonth() + 1 > 9
        ? now.getMonth() + 1
        : '0' + (now.getMonth() + 1).toString()
    let day = now.getDate() > 9 ? now.getDate() : '0' + now.getDate().toString()
    let hour =
      now.getHours() > 9 ? now.getHours() : '0' + now.getHours().toString()
    let minute =
      now.getMinutes() > 9
        ? now.getMinutes()
        : '0' + now.getMinutes().toString()
    let second =
      now.getSeconds() > 9
        ? now.getSeconds()
        : '0' + now.getSeconds().toString()
    let sendDate = `${year}-${month}-${day}`
    let sendTime = `${hour}:${minute}:${second}`
    SetSendDate(sendDate)
    SetSendTime(sendTime)
  }, [])

  useEffect(() => {
    if (auth.isAuthenticated) {
      dispatch(accountList({ _id: auth.user._id }))
      dispatch(transactionList({ userId: auth.user._id }))
    }
  }, [dispatch, auth.isAuthenticated, auth.user._id])

  const handleClose = () => setShow(false)
  const handleShow = () => setShow(true)
  const onSetGasPrice = (e) => {
    try {
      let gasPrice = e.target.value
      SetGasPrice(gasPrice)
    } catch (err) {
      dispatch(errorOccured(err))
    }
  }

  const onSetGasLimit = (e) => {
    try {
      let gasLimit = Number(e.target.value)
      SetGasLimit(gasLimit)
    } catch (err) {
      dispatch(errorOccured(err))
    }
  }

  const onCreateWallet = (e) => {
    let newAccount = web3.eth.accounts.create()
    let data = { _id: user._id, privateKey: newAccount.privateKey }
    dispatch(accountSave(data))
  }

  const onAccountAddressChange = (e) => {
    try {
      let currentAddress = e.target.value
      if (currentAddress.includes('0x') && currentAddress !== '0x') {
        for (let i = 0; i < accounts.length; i++) {
          let privateKey = accounts[i]
          let account = web3.eth.accounts.privateKeyToAccount(privateKey)
          if (account.address === currentAddress) {
            SetPrivateKey(privateKey)
            let provider = new ethers.providers.WebSocketProvider(env.WSS_KEY)
            let wallet = new ethers.Wallet(privateKey, provider)
            wallet.connect(provider)
          }
        }
      }
    } catch (err) {
      dispatch(errorOccured(err))
    }
  }

  const onSearchABIFromAddress = () => {
    dispatch(contractABIFromAddress({ address: contractAddress }))
  }

  // const onSetTokenPrice = async (e) => {
  //   try {
  //     SetTokenPrice(e.target.value)
  //     const gasPrice = await web3.eth.getGasPrice()
  //     SetGasPrice(gasPrice / Math.pow(10, 9))
  //   } catch (err) {
  //     dispatch(errorOccured(err))
  //   }
  // }

  // const onSetTokenAmount = async (e) => {
  //   try {
  //     SetTokenAmount(e.target.value)
  //     const gasPrice = await web3.eth.getGasPrice()
  //     SetGasPrice(gasPrice / Math.pow(10, 9))
  //   } catch (err) {
  //     dispatch(errorOccured(err))
  //   }
  // }

  const onMintNFT = async () => {
    try {
      let account = web3.eth.accounts.privateKeyToAccount(privateKey)
      console.log('account', account)
      let publicKey = account.address
      let amount = await web3.eth.getBalance(account.address)
      if (parseInt(amount) < gasLimit * gasPrice * Math.pow(10, 9)) {
        dispatch(errorOccured('Not Enough Ethers In Your Wallet\n'))
        return
      }
      const nonce = await web3.eth.getTransactionCount(publicKey, 'latest')
      let NFTContract = new web3.eth.Contract(
        contractABI,
        contractAddress,
        account,
      )

      //the transaction
      const tx = {
        from: publicKey,
        to: contractAddress,
        nonce: nonce,
        gas: gasLimit,
        maxPriorityFeePerGas: parseInt(gasPrice * Math.pow(10, 9)),
        value: value.toString() + '000000000',
        data: NFTContract.methods[mintFunction](
          ...mintParameters.map((param) => Number(param)),
        ).encodeABI(),
      }
      const signPromise = web3.eth.accounts.signTransaction(tx, privateKey)
      if (monitorMethod === 'time') {
        signPromise
          .then((signedTx) => {
            let time = new Date(sendDate + ' ' + sendTime)
            let now = new Date()
            if (Math.abs(time - now) > 60 * 1000) {
              // 1 minute
              let data = {
                _id: user._id,
                taskName,
                account: account.address,
                to: contractAddress,
                gasLimit,
                maxPriorityFeePerGas: parseInt(gasPrice * Math.pow(10, 9)),
                value: value.toString() + '000000000',
                data: NFTContract.methods[mintFunction](
                  ...mintParameters.map((param) => Number(param)),
                ).encodeABI(),
                time,
                privateKey,
                monitorMethod,
                monitorFunction,
              }
              dispatch(mintDataSave(data))
            } else {
              web3.eth.sendSignedTransaction(signedTx.rawTransaction, function (
                err,
                hash,
              ) {
                if (!err) {
                  dispatch(
                    successOccured(
                      'The hash of your transaction is: \n' +
                        hash +
                        "\nCheck etherscan's Mempool to view the status of your transaction!",
                    ),
                  )
                } else {
                  dispatch(
                    errorOccured(
                      'Something went wrong when submitting your transaction.\n' +
                        err,
                    ),
                  )
                }
              })
            }
          })
          .catch((err) => {
            console.log('Promise failed:' + err)
            dispatch(errorOccured(err))
          })
      }
      if (monitorMethod === 'function') {
        let now = new Date()
        let data = {
          _id: user._id,
          taskName,
          account: account.address,
          gasLimit,
          maxPriorityFeePerGas: parseInt(gasPrice * Math.pow(10, 9)),
          value: value.toString() + '000000000',
          data: NFTContract.methods[mintFunction](
            ...mintParameters.map((param) => Number(param)),
          ).encodeABI(),
          time: now,
          privateKey,
          to: contractAddress,
          monitorMethod,
          monitorFunction,
        }
        dispatch(mintDataSave(data))
      }
    } catch (err) {
      dispatch(errorOccured(err))
    }
  }

  // const onChangeBoolVariable = async (e) => {
  //   let NFTContract = new web3.eth.Contract(contractABI, contractAddress)

  //   const res = await NFTContract.methods.mintEnabled().call()

  //   if (res) {
  //     SetBoolValue('True')
  //   } else {
  //     SetBoolValue('False')
  //   }
  // }

  const onSelectMonitorFunction = (e) => {
    SetMonitorFunction(e.target.value)
  }

  const onRemoveWallet = (privateKey) => () => {
    let data = { _id: user._id, privateKey }
    dispatch(accountRemove(data))
  }

  const handleImport = () => {
    try {
      web3.eth.accounts.privateKeyToAccount(importKey)
      let data = { _id: user._id, privateKey: importKey }
      dispatch(accountSave(data))
    } catch {
      dispatch(errorOccured('Input correct private key!'))
    } finally {
      setShow(false)
    }
  }
  const onChangeImportKey = (e) => {
    SetImportKey(e.target.value)
  }

  const onChangeMonitorMethod = (e) => {
    SetMonitorMethod(e.target.value)
  }

  const onChangeParameter = (index) => (e) => {
    mintParameters[index] = e.target.value
    console.log(mintParameters)
    SetMintParameters([...mintParameters])
  }

  const onSelectMintFunction = (e) => {
    SetMintFunction(e.target.value)
    SetMintParameters(
      Array(
        contractABI.find((func) => func.name === e.target.value).inputs.length,
      ),
    )
  }

  const onSelectTab = (tab) => () => {
    if (tab !== selectedTab && tab === 1) setSelectedTab(tab)
    if (tab !== selectedTab && tab === 2) {
      getAmounts()
      setSelectedTab(tab)
    }
    if (tab !== selectedTab && tab === 3) {
      if (auth.isAuthenticated) {
        dispatch(transactionList({ userId: auth.user._id }))
      }
      setSelectedTab(tab)
    }
  }

  const getAmounts = useCallback(async () => {
    const amounts = []
    for (let i = 0; i < accounts.length; i++) {
      if (accounts[i] !== null) {
        let account = web3.eth.accounts.privateKeyToAccount(accounts[i])
        let amount = await web3.eth.getBalance(account.address)
        amounts.push(amount)
      }
    }
    setAmounts(amounts)
  }, [accounts])

  useEffect(() => {
    getAmounts()
  }, [accounts, getAmounts])

  const removeTransaction = (tx) => () => {
    dispatch(transactionRemove({ _id: tx._id }))
  }
  const contentForTab1 = (
    <div className="p-4">
      <div style={{ textAlign: 'center' }}>
        <p className="title-1">SELECT WALLET</p>
        <div>
          <select
            className="select-account-address"
            onChange={onAccountAddressChange}
            defaultValue="default"
          >
            <option value={'default'}>Account Address</option>
            {accounts.length > 0
              ? accounts
                  .filter((account) => account !== null)
                  .map((value, key) => {
                    let account = web3.eth.accounts.privateKeyToAccount(value)
                    return (
                      <option key={key} value={account.address}>
                        {account.address}
                      </option>
                    )
                  })
              : null}
          </select>
        </div>
        <p className="font-size-1">PRIVATE KEY: {privateKey}</p>
      </div>

      <div style={{ textAlign: 'center' }} className="mt-5">
        <p className="title-1">NFT MINTING CONTRACT</p>
        <div className="d-flex justify-content-center">
          <div className="nft-address-box">
            <input
              type="text"
              value={contractAddress}
              onChange={(e) => SetContractAddress(e.target.value)}
            ></input>
            <button
              className="nft-address-search"
              onClick={onSearchABIFromAddress}
            >
              <img
                alt="IMG"
                src="data:image/png;base64,"
              ></img>
            </button>
          </div>
        </div>
      </div>

      <div style={{ textAlign: 'center' }} className="mt-5">
        <p className="title-1">SELECT MINT FUNCTION</p>
        <div className="account-address-box">
          <select
            className="select-account-address w-70"
            onChange={onSelectMintFunction}
          >
            <option>Select Mint Function</option>
            {contractFunction
              ? contractFunction.map((value, key) => {
                  return (
                    <option value={value.name} key={key}>
                      {value.name}
                    </option>
                  )
                })
              : null}
          </select>
          <div className="mt-2">
            <span className="p-0 font-size-1">{'Value To Send (GWei)'}</span>
            <div className="d-flex justify-content-center">
              <div className="nft-address-box">
                <input
                  type="number"
                  className="w-100"
                  min={0}
                  value={value}
                  onChange={(e) => SetValue(e.target.value)}
                />
              </div>
            </div>
          </div>
          {contractABI.length && mintFunction !== '' ? (
            contractABI
              .find((func) => func.name === mintFunction)
              .inputs.map((input, index) => {
                return (
                  <div key={index} className="mt-2">
                    <span className="p-0 font-size-1">{`${input.name} (${input.type})`}</span>
                    <div className="d-flex justify-content-center">
                      <div className="nft-address-box">
                        <input
                          type="text"
                          className="w-100"
                          onChange={onChangeParameter(index)}
                        />
                      </div>
                    </div>
                  </div>
                )
              })
          ) : (
            <></>
          )}
        </div>
      </div>

      <div
        style={{
          textAlign: 'start',
          border: '1px solid rgb(64, 64, 64)',
          borderRadius: '6px',
        }}
        className="mt-5 w-70 m-auto d-flex p-2"
      >
        <div className="flex-grow-1 px-3">
          <p className="title-1">GAS SETTINGS</p>
          <div className="d-flex">
            <p className="font-size-1 pt-2">GAS PRICE</p>
            <div className="d-flex flex-column align-items-center">
              <div className="nft-address-box">
                <input
                  disabled={gasPriceAuto ? true : false}
                  value={gasPrice}
                  onChange={onSetGasPrice}
                />
              </div>
              <div>
                <div
                  className="button"
                  onClick={() => SetGasPriceAuto(!gasPriceAuto)}
                >
                  <p
                    className={clsx('font-size-1', {
                      'color-yellow': gasPriceAuto,
                    })}
                  >
                    AUTO
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div className="d-flex">
            <p className="font-size-1 pt-2">GAS LIMIT</p>
            <div className="d-flex flex-column align-items-center">
              <div className="nft-address-box">
                <input
                  disabled={gasLimitAuto ? true : false}
                  placeholder={250000}
                  value={gasLimit}
                  onChange={onSetGasLimit}
                />
              </div>
              <div>
                <div
                  className="button"
                  onClick={() => SetGasLimitAuto(!gasLimitAuto)}
                >
                  <p
                    className={clsx('font-size-1', {
                      'color-yellow': gasLimitAuto,
                    })}
                  >
                    AUTO
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="flex-grow-1">
          <div className="d-flex">
            <input
              className="form-check-input bg-warning"
              type="radio"
              name="flexRadioDefault"
              id="flexRadioDefault1"
              value="time"
              onChange={onChangeMonitorMethod}
              checked={monitorMethod === 'time'}
            />
            <p className="title-1 mb-2 mx-2">MINTING TIME</p>
          </div>
          <div className="nft-address-box w-80">
            <input
              type="date"
              value={sendDate}
              disabled={monitorMethod !== 'time'}
              onChange={(e) => SetSendDate(e.target.value)}
            ></input>
            <input
              type="time"
              value={sendTime}
              disabled={monitorMethod !== 'time'}
              onChange={(e) => SetSendTime(e.target.value)}
            ></input>
          </div>
          <div className="d-flex mt-3">
            <input
              className="form-check-input bg-warning"
              type="radio"
              name="flexRadioDefault"
              id="flexRadioDefault1"
              value="function"
              onChange={onChangeMonitorMethod}
              checked={monitorMethod === 'function'}
            />
            <p className="title-1 mb-2 mx-2">MONITOR FUNCTION</p>
          </div>
          <div className="w-80">
            <select
              className="select-account-address w-100"
              disabled={monitorMethod !== 'function'}
              onChange={onSelectMonitorFunction}
            >
              <option>Select Monitor Function</option>
              {monitorFunctions
                ? monitorFunctions.map((value, key) => {
                    return (
                      <option value={value.name} key={key}>
                        {value.name}
                      </option>
                    )
                  })
                : null}
            </select>
          </div>
        </div>
      </div>

      <div className="w-70 m-auto mt-3">
        <p className="font-size-1 mb-1">TASK NAME</p>
        <div className="nft-address-box w-30">
          <input onChange={(e) => setTaskName(e.target.value)} />
        </div>
      </div>

      <div className="create-button text-black" onClick={onMintNFT}>
        CREATE TASK
      </div>
    </div>
  )
  const contentForTab2 = () => {
    return (
      <div className="p-4">
        <div className="d-flex justify-content-center">
          <div className="px-3">
            <div
              className="button wallet-button font-size-1 font-weight-bold"
              onClick={onCreateWallet}
            >
              CREATE A WALLET
            </div>
          </div>
          <div className="px-3">
            <div
              className="button wallet-button font-size-1 font-weight-bold"
              onClick={handleShow}
            >
              IMPORT A WALLET
            </div>
          </div>
        </div>

        <div>
          <p className="title-1">WALLETS LIST</p>
          <div className="list-box height-4 p-4">
            <div className="d-flex flex-wrap">
              {accounts.length > 0
                ? accounts
                    .filter((account) => account !== null)
                    .map((value, key) => {
                      let account = web3.eth.accounts.privateKeyToAccount(value)
                      let amount =
                        amounts[key] === undefined ? 0 : Number(amounts[key])
                      return (
                        <div
                          key={key}
                          value={account.address}
                          className="d-flex w-100"
                        >
                          <div className="flex-grow-1">{account.address}</div>
                          <div
                            className="button text-danger px-4"
                            onClick={onRemoveWallet(value)}
                          >
                            REMOVE
                          </div>
                          <div className="px-1">
                            {Math.floor(amount / Math.pow(10, 16)) / 100}
                          </div>
                        </div>
                      )
                    })
                : null}
            </div>
          </div>
        </div>
      </div>
    )
  }
  const contentForTab3 = (
    <div className="p-4">
      <div>
        <p className="title-1">TASKS LIST</p>
        <div className="list-box height-3 p-4">
          <div className="d-flex flex-wrap">
            {transactions
              .filter((tx) => tx.status === 'pending')
              .map((tx, index) => (
                <div className="d-flex w-100" key={index}>
                  <div className="flex-grow-1">{tx.taskName}</div>
                  <div
                    className="button text-danger"
                    onClick={removeTransaction(tx)}
                  >
                    REMOVE
                  </div>
                </div>
              ))}
          </div>
        </div>
      </div>
      <div className="mt-4">
        <p className="title-1">LOGS</p>
        <div className="list-box height-3 p-4">
          {transactions
            .filter((tx) => tx.status !== 'pending')
            .map((tx, index) => (
              <div className="d-flex w-100" key={index}>
                <div className="flex-grow-1">{tx.taskName}</div>
                <div>{tx.status}</div>
              </div>
            ))}
        </div>
      </div>
    </div>
  )

  if (auth.isAuthenticated) {
    return (
      <Header>
        <div className="body-container">
          <div className="panel d-flex flex-column">
            <div className="panel-header d-flex">
              <div
                className={clsx({ highlight: selectedTab === 1 })}
                onClick={onSelectTab(1)}
              >
                MINTING SETTINGS
              </div>
              <div className="seperator">|</div>
              <div
                className={clsx({ highlight: selectedTab === 2 })}
                onClick={onSelectTab(2)}
              >
                WALLET MANAGER
              </div>
              <div className="seperator">|</div>
              <div
                className={clsx({ highlight: selectedTab === 3 })}
                onClick={onSelectTab(3)}
              >
                TASKS
              </div>
            </div>
            <div className="panel-body flex-grow-1">
              {selectedTab === 1
                ? contentForTab1
                : selectedTab === 2
                ? contentForTab2()
                : contentForTab3}
            </div>
          </div>
          <Modal show={show} onHide={handleClose}>
            <Modal.Header>
              <Modal.Title>Input PrivateKey</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <input
                placeholder="0x"
                style={{ width: '100%' }}
                onChange={onChangeImportKey}
              ></input>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={handleClose}>
                Close
              </Button>
              <Button variant="primary" onClick={handleImport}>
                Import
              </Button>
            </Modal.Footer>
          </Modal>
        </div>
      </Header>
    )
  } else {
    return <Navigate to={'/'} />
  }
}

export default Home
