import {
  ACCOUNT_LIST_REQUEST,
  ACCOUNT_REMOVE_REQUEST,
  ACCOUNT_SAVE_REQUEST,
} from '../../constants/admin/actionTypes'

export const accountSave = (data) => ({
  type: ACCOUNT_SAVE_REQUEST,
  data,
})

export const accountRemove = (data) => ({
  type: ACCOUNT_REMOVE_REQUEST,
  data,
})

export const accountList = () => ({
  type: ACCOUNT_LIST_REQUEST,
})
