import { Request } from './basic'
import { put, takeLatest } from 'redux-saga/effects'
import {
  TRANSACTION_LIST_REQUEST,
  TRANSACTION_LIST_SUCCESS,
  TRANSACTION_REMOVE_REQUEST,
  ERROR_OCCURED,
  SUCCESS_OCCURED,
} from '../constants/actionTypes'
import { errorOccured } from '../action/error.action'

function* TransactionRemove(action) {
  try {
    const responseData = yield Request(
      'transaction/remove',
      'DELETE',
      action.data,
    )
    if (responseData.success) {
      yield put({
        type: SUCCESS_OCCURED,
        message: 'Remove account successfully!',
      })

      yield put({
        type: TRANSACTION_LIST_SUCCESS,
        data: responseData.success.transactions,
      })
    } else yield put(errorOccured(responseData))
  } catch (err) {
    yield put({
      type: ERROR_OCCURED,
      message: 'The number of wallet is already 5.',
    })
  }
}

function* GetTransactions(action) {
  try {
    const responseData = yield Request('transaction', 'POST', action.data)
    if (responseData) {
      console.log(responseData)
      yield put({ type: TRANSACTION_LIST_SUCCESS, data: responseData })
    }
  } catch (err) {
    console.log(err)
  }
}

export default function* accountSaga() {
  yield takeLatest(TRANSACTION_LIST_REQUEST, GetTransactions)
  yield takeLatest(TRANSACTION_REMOVE_REQUEST, TransactionRemove)
}
