import { Request } from '../basic'
import { put, takeLatest } from 'redux-saga/effects'
import {
  USER_LIST_REQUEST,
  USER_LIST_SUCCESS,
  USER_UPDATE_REQUEST,
  SUCCESS_OCCURED,
  ERROR_OCCURED,
} from '../../constants/admin/actionTypes'
import { errorOccured } from '../../action/error.action'

function* UserUpdate(action) {
  try {
    const responseData = yield Request(
      'admin/admin/update',
      'POST',
      action.data,
    )
    if (responseData.success) {
      yield put({
        type: SUCCESS_OCCURED,
        message: 'Update account successfully!',
      })

      yield put({
        type: USER_LIST_SUCCESS,
        data: responseData.success.users,
      })
    } else yield put(errorOccured(responseData))
  } catch (err) {
    yield put({
      type: ERROR_OCCURED,
      message: 'Update error',
    })
  }
}

function* GetUsers(action) {
  try {
    const responseData = yield Request('admin/admin', 'GET')
    if (responseData) {
      console.log(responseData)
      yield put({ type: USER_LIST_SUCCESS, data: responseData })
    }
  } catch (err) {
    console.log(err)
  }
}

export default function* accountSaga() {
  yield takeLatest(USER_UPDATE_REQUEST, UserUpdate)
  yield takeLatest(USER_LIST_REQUEST, GetUsers)
}
